	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"One Good Thing About Germany","date":"26 Jan 2007","slug":"/2007-01-26-one-good-thing-about-germany/","tags":"Y2007"},"html":"<p><!-- ID = 38068 -->Man: Hey, are you guys from Germany?<br />Tourists: Ja?<br />Man: I'll tell you one good thing about Germany -- the beer, the food, and the women.<br />Tourist: Ja.</p>\n<p>via <a href=\"http://www.overheardinnewyork.com/archives/008834.html\">Overheard in New York</a>, Jan 25, 2007</p>"},"next":{"fields":{"title":"What Old People Listen To","date":"25 Jan 2007","slug":"/2007-01-25-what-old-people-listen-to/"}},"previous":{"fields":{"title":"No thanks, I'll just drive my radio","date":"29 Jan 2007","slug":"/2007-01-29-no-thanks-ill-just-drive-my-radio/"}}},{"node":{"fields":{"title":"What Old People Listen To","date":"25 Jan 2007","slug":"/2007-01-25-what-old-people-listen-to/","tags":"Y2007"},"html":"<p><a href=\"http://www.onezblog.de/item/250\">Soeren asked me</a> what I would consider the best albums of all time or most important to me. I decided both \"best\" and \"important\" were uninteresting (and <a href=\"http://www.rollingstone.com/500albums\">best</a> has already been done). I decided to list \"favorite\" instead, albums I like and listen to, even though they may not be the best, and maybe next week my list would be different. I did limit myself to one pick per artist. In order:</p>\n<ul>\n<li>Dirty Mind, Prince</li>\n<li>Rocket to Russia, Ramones</li>\n<li>Born to Run, Bruce Springsteen</li>\n<li>London Calling, The Clash</li>\n<li>Making Movies, Dire Straits</li>\n<li>Achtung Baby, U2</li>\n<li>Talk Show, Go-Go's</li>\n<li>Planet Punk, Die Ärzte</li>\n<li>So, Peter Gabriel</li>\n</ul>\n<p>He listed nine, so I did too (though the 9th was the hardest to pick). For the moment I won't explain my choices, maybe later. Feel free to tell me in the comments why my picks suck.</p>"},"next":{"fields":{"title":"Linux Can Be Dangerous","date":"25 Jan 2007","slug":"/2007-01-25-linux-can-be-dangerous/"}},"previous":{"fields":{"title":"One Good Thing About Germany","date":"26 Jan 2007","slug":"/2007-01-26-one-good-thing-about-germany/"}}},{"node":{"fields":{"title":"Linux Can Be Dangerous","date":"25 Jan 2007","slug":"/2007-01-25-linux-can-be-dangerous/","tags":"Y2007"},"html":"<p><a href=\"http://www.dilbert.com/comics/dilbert/archive/dilbert-20070125.html\">Today's Dilbert</a> shows how Linux can be dangerous for your job... particularly (but not only) if you are a super-intelligent space alien.</p>\n<p><a href=\"http://www.dilbert.com/comics/dilbert/archive/dilbert-20070125.html\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/01/dilbertlinux.gif\" alt=\"Dilbertlinux\" /></a></p>"},"next":{"fields":{"title":"Wine School 2","date":"23 Jan 2007","slug":"/2007-01-23-wine-school-2/"}},"previous":{"fields":{"title":"What Old People Listen To","date":"25 Jan 2007","slug":"/2007-01-25-what-old-people-listen-to/"}}}],"pathPrefix":"","first":false,"last":false,"index":160,"pageCount":1101}}

